.open_map .img_logo {
    filter: var(--img-filter) drop-shadow(0 0 2px var(--mdb-form-control-border-color));
}

.main_model {
    position: fixed;
    inset: 0;
    z-index: 5;
    width: 100%;
    background: var(--mdb-body-bg);
    transition: all 0.3s ease;
}
.open_map .main_model {
    left: 0;
    top: unset;
    bottom: 3%;
    right: unset;
    width: 40vw;
    max-width: 300px;
    height: 25vh;
    min-height: unset;
    background: transparent;
}

#asistent_model {
    position: absolute;
    inset: 0;
    bottom: unset;
    height: 100vh;
    width: inherit;
    /* max-width: 1300px; */
    opacity: 1;
    visibility: visible;
    transition: opacity 0.3s ease, visibility 0.3s ease;
}
.open #asistent_model {
    opacity: 0;
    visibility: hidden;
}
.open_map #asistent_model {
    height: inherit;
    max-width: 300px;
}
.open_map .open #asistent_model {
    opacity: 1;
    visibility: visible;
}

.chat_form {
    position: absolute !important;
    bottom: 2%;
    top: unset;
    left: 50%;
    width: 94%;
    max-width: 1300px;
    transform: translateX(-50%);
    overflow: visible !important;
}
.open_map .chat_form {
    background: transparent !important;
    box-shadow: none !important;
}

.chat_form textarea {
    background: transparent;
    border: none !important;
    border-radius: var(--border-radius);
    field-sizing: content;
    height: auto;
    min-height: 25px;
    max-height: 125px;
    visibility: visible;
}
.open_map .chat_form textarea {
    transform: scale(0);
    min-height: 0;
    max-height: 0;
    position: absolute;
    visibility: hidden;
}

.chat_form .chat_controls {
    display: flex;
    justify-content: flex-start;
    flex-direction: row-reverse;
    padding: 0.5rem;
}
.chat_form .chat_controls button {
    box-shadow: none !important;
}
.chat_form .chat_controls i {
    font-size: 1.2rem;
}

.chat_form .chat_controls .btn:hover {
    transform: scale(1.1);
}
.chat_form .chat_controls .btn:focus {
    border: 1px solid var(--mdb-body-color);
}
.open_map .chat_form .chat_controls {
    justify-content: flex-end;
    padding: 0;
}
.chat_form .chat_controls #openChat {
    margin: 0 auto 0 0 !important;
}
.open_map .chat_form .chat_controls #openChat,
.open_map .chat_form .chat_controls #changeScene,
.open_map .chat_form .chat_controls #chatForm_submit,
.open_map .chat_form .chat_controls #floatSettings {
    display: none;
}
.open_map .chat_form .chat_controls #chatMicrophone,
.open_map .chat_form .chat_controls #chatOpenMap {
    background: var(--var-detail);
    color: white;
}

.chat_form .tab {
    --size: 40px;
    position: absolute;
    top: calc((var(--size) / -1) + 10px);
    left: 0;
    background: var(--mdb-card-bg);
    height: var(--size);
    text-align: center;
    border: none;
    padding-inline: 10px;
    border-radius: var(--border-radius) var(--border-radius) 0 0;
    outline: none;
}
.chat_form .tab::before {
    --size: calc(var(--border-radius) * 2);
    content: "";
    position: absolute;
    top: 10px;
    left: 100%;
    height: var(--size);
    width: var(--size);
    border-radius: 0 var(--border-radius);
    box-shadow: -2px 2px 0 2px var(--mdb-card-bg);
    cursor: default;
}
.chat_form .tab i.fas {
    transform: rotate(0deg);
    transition: transform 0.3s ease;
}
.open .chat_form .tab i.fas {
    transform: rotate(180deg);
}
.chat_form .tab:focus i,
.chat_form .tab:hover i {
    color: var(--var-detail_flat);
}
.chat_form .tab:focus i.fas,
.chat_form .tab:hover i.fas {
    transform: scale(1.1) rotate(0deg);
}
.open_map .chat_form .tab {
    display: none;
}

.chat_output {
    position: absolute;
    left: 50%;
    top: 80px;
    width: 94%;
    height: 100%;
    max-width: 1330px;
    max-height: calc(100vh - 240px);
    display: flow-root;
    overflow-x: hidden;
    overflow-y: auto;
    padding-inline: 15px;
    transform-origin: bottom center;
    transform: scaleY(0) translateX(-50%);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease, visibility 0.3s ease;
}
.chat_output.show {
    transform: scaleY(1) translateX(-50%);
    visibility: visible;
    opacity: 1;
}
.open_map .chat_output {
    display: none;
}

.chat_output .chat_msg {
    overflow: hidden;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    margin: 20px 0 0 0;
    transform: scale(0);
    transition: opacity 0.3s ease, visibility 0.3s ease, margin 0.5s var(--transitionCubicVal);
}

.chat_output .chat_msg.show {
    opacity: 1;
    visibility: visible;
    margin: 0;
    transform: scale(1);
}
.chat_output .chat_msg.chat_open {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    z-index: -2;
}
.chat_output .user_submit {
    opacity: 1;
    visibility: visible;
    position: unset;
    transition: opacity 0.3s ease, visibility 0.3s ease;
}
.chat_output .user_submit.active {
    opacity: 0;
    visibility: hidden;
    position: absolute;
}
.open .chat_output .user_submit.active {
    opacity: 1;
    visibility: visible;
    position: unset;
}
.open .chat_output .chat_msg.chat_open {
    opacity: 1;
    visibility: visible;
    position: unset;
    z-index: unset;
}
.chat_output .chat_msg .msg_user {
    width: fit-content;
    max-width: 85%;
    border-radius: var(--border-radius);
}

.chat_output .chat_msg .msg_response {
    width: 100%;
    max-width: 100%;
    text-align: justify;
    margin-block: 20px;
    background: var(--mdb-body-bg);
}

.chat_output .chat_msg img {
    border-radius: var(--border-radius);
}

.chat_output .chat_msg .chat_img {
    padding: 0;
    width: 30%;
    max-width: 500px;
    margin-block: 20px;
    border-radius: var(--border-radius);
}

